@import '../variables';

.sort {
  position: relative;
  width: 253px;

  &__label {
    display: flex;
    align-items: center;

    svg {
      margin-right: 8px;
    }

    b {
      margin-right: 8px;
      text-align: center;
      white-space: nowrap;

      @media (max-width: 300px) {
        white-space: pre-wrap;
      }
    }

    div {
      color: $orange;
      border-bottom: 1px dashed $orange;
      cursor: pointer;
      white-space: nowrap;
    }
  }

  &__popup {
    z-index: 1;
    position: absolute;
    right: 0;
    margin-top: 15px;
    background: #ffffff;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.09);
    border-radius: 10px;
    overflow: hidden;
    padding: 10px 0;
    width: 160px;

    ul {
      overflow: hidden;

      li {
        padding: 12px 20px;
        cursor: pointer;

        &.active,
        &:hover {
          background: rgba(254, 95, 30, 0.05);
        }

        &.active {
          font-weight: bold;
          color: $orange;
        }
      }
    }
  }
}
